<template>
  <li class="cart__item product" v-if="item.product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>

    <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар"
              @click="amount = amount > 1 ? amount - 1 : amount = 1">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model="amount" name="count">

      <button type="button" aria-label="Добавить один товар" @click="amount = amount + 1">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ item.product.price * item.amount | numberFormat}} $
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
    @click="cartStore.deleteCartProductAPI(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { useCartStore } from '@/stores/cartStores';

export default {
  setup() {
    const cartStore = useCartStore();

    return {
      cartStore
    }
  },
  name: 'CartItem',
  props: ['item'],
  filters: {numberFormat},
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.cartStore.updateCartProductAmountAPI(this.item.productId, value);
      }
    }
  }
};
</script>

