import { defineStore } from 'pinia';
import { useProductStore } from '@/stores/productStores';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export const useCartStore = defineStore('cartStore', {
  state: () => ({
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    orderInfo: null,
  }),
  actions: {
    loadOrderInfo(orderId) {
      return axios.get(API_BASE_URL + `/api/orders/${orderId}`, {
        params: {
          userAccessKey: this.userAccessKey,
        }
      })
        .then((r) => this.orderInfo = r.data)
    },
    updateOrderInfo(orderInfo) {
      this.orderInfo = orderInfo;
    },
    resetCart() {
      this.cartProducts = [];
      this.cartProductsData = [];
    },
    loadCart() {
      return axios.get(API_BASE_URL + `/api/baskets`, {
        params: {
          userAccessKey: this.userAccessKey,
        }
      })
        .then((r) => {
          if (!this.userAccessKey) {
            localStorage.setItem('userAccessKey', r.data.user.accessKey)
            this.userAccessKey = r.data.user.accessKey;
          }

          this.cartProductsData = r.data.items;
          this.syncCartProducts();
        });
    },
    addProductToCart(productId, amount) {
      return axios.post(API_BASE_URL + '/api/baskets/products', {
        productId: productId,
        quantity: amount
      }, {
        params: {
          userAccessKey: this.userAccessKey,
        }
      })
        .then(r => {
          this.cartProductsData = r.data.items;
          this.syncCartProducts();
        })
    },
    updateCartProductAmountAPI(productId, amount) {
      this.updateCartProductAmount(productId, amount);
      return axios.put(API_BASE_URL + '/api/baskets/products', {
        productId: productId,
        quantity: amount
      }, {
        params: {
          userAccessKey: this.userAccessKey,
        }
      })
        .then(r => {
          this.cartProductsData = r.data.items;
        })
        .catch(() => {
          this.syncCartProducts();
        });
    },
    deleteCartProductAPI(productId) {
      this.deleteCartProduct(productId);
      return axios.delete(API_BASE_URL + '/api/baskets/products', {
        data: {
          productId: productId,
        },
        params: {
          userAccessKey: this.userAccessKey,
        }
      })
        .then(r => {
          this.cartProductsData = r.data.items;
        })
        .catch(() => {
          this.syncCartProducts();
        });
    },
    syncCartProducts() {
      this.cartProducts = this.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity,
        }
      })
    },
    setUserAccessKey(key) {
      this.userAccessKey = key;
    },
    updateCartProductAmount(productId, amount) {
      const item = this.cartProducts.find(item => item.productId === productId);

      if (!item)
        return false;

      item.amount = amount;
    },
    deleteCartProduct(productId) {
      this.cartProducts = this.cartProducts.filter(item => item.productId !== productId);
    },
  },
  getters: {
    getUserAccessKey() {
      return this.userAccessKey;
    },
    cartDetailProducts() {
      const productStore = useProductStore();
      if (!productStore.getProducts.length) return false;
      return this.cartProducts.map(item => {
        return {
          ...item,
          product: productStore.getProducts.find(p => p.id === item.productId)
        }
      })
    },
    cartTotalPrice() {
      if (!this.cartDetailProducts.length) return false;
      return this.cartDetailProducts.reduce((acc, item) => {
        if (item.product) return (item.product.price * item.amount) + acc;
      }, 0);
    },
    cartTotalAmount() {
      if (!this.cartDetailProducts.length) return false;
      return this.cartDetailProducts.reduce((acc, item) => {
        if (item.product) return item.amount + acc;
      }, 0);
    },
    cartTotalItems() {
      return this.cartProducts.length;
    }
  }
});
