<template>
  <div>
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>
    <div class="content__catalog">
      <ProductFilter :categories="filterCategories"
                     :price-from.sync="filterPriceFrom"
                     :price-to.sync="filterPriceTo"
                     :category-id.sync="filterCategoryId"
                     :color.sync="filterColor"
      />
      <section class="catalog">
        <div v-if="productsLoading">Загрузка товара...</div>
        <div v-if="productsLoadingFailed">
          Произошла ошибка при загрзке товара...
          <button @click.prevent="productsLoad">Попробовать еще раз</button>
        </div>
        <ProductList :products="products"/>
        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>
      </section>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter';
import { useProductStore } from '@/stores/productStores';
import { API_BASE_URL } from '@/config';

export default {
  setup() {
    const productStore = useProductStore();

    return {
      productStore
    }
  },
  name: 'MainPage',
  components: { ProductFilter, BasePagination, ProductList },
  data() {
    return {
      loadProducts: [],
      page: 1,
      productsPerPage: 9,

      filterPriceFrom: null,
      filterPriceTo: null,
      filterCategoryId: null,
      filterColor: null,
      filterCategories: [],

      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,

      colorsData: null,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.prepareProducts() : false;
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  created() {
    this.productsLoad();
  },
  methods: {
    productsLoad() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(API_BASE_URL + `/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColor,
          }
        }).then((r) => this.productsData = r.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
      }, 0);
    },
    prepareProducts() {
        return this.productsData.items.map(product => {
          return {
            ...product,
            image: product.image.file.url,
          }
        })
    }
  },
  watch: {
    page() {this.productsLoad()},
    filterPriceFrom() {this.productsLoad()},
    filterPriceTo() {this.productsLoad()},
    filterCategoryId() {this.productsLoad()},
    filterColor() {this.productsLoad()}
  }
};
</script>

