<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{cartStore.cartTotalAmount}} товар(а/ов)
      </span>
    </div>

    <img src="@/assets/loading.gif" alt="Загрузка..." v-if="formDataLoading"/>
    <section class="cart" v-else-if="!formDataLoading">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

            <base-form-text v-model="formData.name"
                            title="ФИО"
                            placeholder="Введите ваше полное имя"
                            :error="formError.name"/>

            <base-form-text v-model="formData.address"
                            title="Адрес доставки"
                            placeholder="Введите ваш адрес"
                            :error="formError.address"/>

            <base-form-text v-model="formData.phone"
                            title="Телефон"
                            placeholder="Введите ваш телефон"
                            :error="formError.phone"/>

            <base-form-text v-model="formData.email"
                            title="Email"
                            placeholder="Введи ваш Emai"
                            :error="formError.email"/>

            <base-form-text-area v-model="formData.comment"
                            title="Комментарий к заказу"
                            placeholder="Ваши пожелания"
                            :error="formError.comment"/>

          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <cart-item-info v-for="item in cartStore.cartDetailProducts" :key="item.productId" :item="item"/>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{cartStore.cartTotalAmount}}</b> товар(а/ов)npm на сумму <b>{{cartStore.cartTotalPrice}} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formErrorMessage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormField from '@/components/BaseFormField.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { useCartStore } from '@/stores/cartStores';
import CartItemInfo from '@/components/CartItemInfo.vue';

export default {
  setup() {
    const cartStore = useCartStore();

    return {
      cartStore
    }
  },
  name: 'OrderPage',
  components: { CartItemInfo, BaseFormTextArea, BaseFormText, BaseFormField },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',

      formDataLoading: false,
    }
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.formDataLoading = true;

      axios.post(API_BASE_URL + '/api/orders', {
        ...this.formData
      }, {
        params: {
          userAccessKey: this.cartStore.getUserAccessKey
        }
      })
        .then((r) => {
          this.cartStore.resetCart();
          this.cartStore.updateOrderInfo(r.data);
          this.$router.push({name: 'orderInfo', params: {id: r.data.id}})
        })
        .catch((e) => {
          this.formError = e.response?.data.error.request || {};
          this.formErrorMessage = e.response?.data.error.message;
        })
        .then(() => this.formDataLoading = false)
    }
  }
};
</script>

